* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country_info {
  display: flex;
}

ul {
  list-style: none;
  padding: 0;
}

.country-info_item {
  display: flex;
}

.country-info_item:not(:last-child) {
  margin-bottom: 15px;
}

.country-info_flag {
  margin-right: 10px;
}

.country-info_name {
  font-weight: 700;
}

.contry-info_details span {
  font-weight: 500;
}
